@import "../mixins";

.roadmap-t {
  background-image: url("../img/roadmap/maib-bg.png");
  background-size: 1920px 1200px;
  background-repeat: repeat;
  background-position: center;
  padding-top: 7rem;
  padding-bottom: 100rem;
  background-color: #171717;
  position: relative;
}

.roadmap__inner {
  display: flex;
}

.roadmap__title {
  font-weight: 700;
  font-size: 4.2rem;
  color: var(--white);
  text-transform: uppercase;
  text-align: left;
}

.road__img {
  width: auto;
  height: 485px;
}

.road__info {
  position: absolute;
  margin: 9rem 1rem;
}

.road__title {
  font-weight: 700;
  color: var(--white);
  font-size: 4.2rem;
  text-transform: uppercase;
  margin-bottom: 3rem;
  text-align: left;
}

.road__text {
  display: block;
  display: flex;
  font-weight: 400;
  color: var(--white);
  font-size: 1.8rem;
  margin-bottom: 3rem;
  text-align: left;
  position: relative;
  align-items: center;
}

.road__progress {
  width: 100%;
  max-width: 30px;
  height: 30px;
  margin-right: 1.5rem;
}

.roadmap__nav {
  display: flex;
  justify-content: space-between;
}

.road__slider {
  display: flex;
}

.road__prev,
.road__next {
  position: relative;
  width: 54px;
  height: 54px;
  background: #e99126;
  border-radius: 50%;
  cursor: pointer;

  &::after {
    display: block;
    position: absolute;
    content: "";
    width: 14px;
    height: 20px;
    background-image: url("../img/svg/slide-arrow.svg");
    background-position: center center;
    background-repeat: no-repeat;
    left: 22px;
    top: 16px;
  }
}

.road__prev {
  margin-right: 2rem;

  &::after {
    transform: rotate(180deg);
    left: 18px;
  }
}

.roadmap-t {
  padding-bottom: 108rem;
  background-color: #171717;
  position: relative;
  padding-top: 7rem;
  z-index: 2;
  & .container {
    z-index: 1;
    position: relative;
  }
  &_swiper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 0;
    &-wrapper {
      z-index: 0;
    }
    &-slide {
      width: 2266px;
      height: 1206px;
      background-color: transparent;
      background-image: url("../img/roadmap/bg.png");
      background-size: 2266px 1206px;

      .road__block {
        position: relative;
      }

      .road__item {
        min-width: 270px;
        display: flex;
        flex-direction: column;

        &:nth-child(1) {
          position: absolute;
          left: 12px;
          top: 227px;
        }
        &:nth-child(2) {
          position: absolute;
          left: 312px;
          top: 705px;
        }
        &:nth-child(3) {
          position: absolute;
          left: 652px;
          top: 227px;
        }
        &:nth-child(4) {
          position: absolute;
          left: 972px;
          top: 705px;
        }
        &:nth-child(5) {
          position: absolute;
          left: 1292px;
          top: 227px;
        }
        &:nth-child(6) {
          position: absolute;
          left: 1612px;
          top: 705px;
        }
        &:nth-child(7) {
          position: absolute;
          left: 1932px;
          top: 227px;
        }
      }
    }
  }
}

@media screen and (max-width: 1350px) {
  .roadmap-t_swiper-slide {
    width: 1603px;
    height: 853px;
    background-size: 1603px 853px;
  }
  .roadmap-t_swiper-slide .road__item {
    min-width: 200px;
  }
  .road__info {
    position: absolute;
    margin: 4rem 2rem;
  }
  .road__img {
    width: auto;
    height: 420px;
  }
  .road__title {
    font-size: 3.2rem;
  }
  .road__text {
    font-size: 1.3rem;
  }
  .road__img {
    width: auto;
    height: 340px;
  }
